<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Whislist</h1>
        <div class="chartWrap"> <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>  <a href="javascript:void(0)" @click="$router.push('search')" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <section class="downListWrap">
      <div class="commonPro">
        <ul class="clearfix" :class="{'turnLine':showStyle==1,'Prolist':showStyle==2}">
          <li v-for="(item,index) in wishes.result" :key="index">
                <div class="tips_cutOff" v-if="item.productSpu.percentage"> {{item.productSpu.percentage}} OFF </div>
                <div class="tips_sale" v-if="item.productSpu.sale"> </div>
                <div class="imgWrap">
                  <img class="img" :src="item.productSpu.spuMainImage">
                </div>
                <div class="rightMain">
                  <div class="name"> {{item.productSpu.name}} </div>
                  <div class="downWrap">
                    <div class="bottomPrice">
                      <p class="red"> {{ getStr(item.productSpu.priceRange) }} AED  </p>
                      <p class="gray"> {{ getStr(item.productSpu.priceRangeLine) }} AED </p>
                      <p v-if="item.productSpu.isTypeFour==1"> Free Shipping </p>
                    </div>
                    <div class="downShare">
                      <p class="others"> {{item.productSpu.oterNum}} Other Items </p>
                      <div :class="{'active':item.productSpu.cart}" @click="addChart(item.productSpu)"> <span class="cart iconfont"> &#xe669; </span> </div>
                      <div :class="{'active':item.productSpu.share}"  @click.stop="shareShow(item)"> <span class="share1 iconfont"> &#xe650; </span> </div>
                       <div :class="{'active':item.productSpu.share}" @click="deleteWish(item.id)"> <span class="share iconfont"> &#xe609; </span> </div>                     
                    </div>
                  </div>
                </div>
          </li>
        </ul>
      </div>
    </section>

  <!-- 添加购物车 -->
    <add-cart ref="addCart"> </add-cart>

  </div>
</template>

<script>

import {mapGetters} from 'vuex';
export default {
  name: 'whislist',
  data () {
    return {
      sortVisible:false,
      searchValue:"",
      sortValue:2,
      sortType:[
        { value:1,
          label:'Price:Low to High'
        },
        {value:2,
          label:'Price:High to Low'
        },
        {value:3,
          label:'Best Sale'
        },
        {value:4,
          label:'High Rated'
        }
      ],
      showStyle:1,
      show:false,
      toggle:{
        "Categories":1,
        "Brand":0
      },
      priceRange:["",""],
      radioValue:1,
      radioList:[
        {
          "label":"Sub-Categories1 (99)",
          "value":1
        },
        {
          "label":"Sub-Categories2",
          "value":2
        },
        {
          "label":"Sub-Categories3",
          "value":3
        }
      ],
      radioValue2:1,
      radioList2:[
        {
          "label":"Sub-Brand1 (99)",
          "value":1
        },
        {
          "label":"Sub-Brand2",
          "value":2
        },
        {
          "label":"Sub-Brand3",
          "value":3
        },
        {
          "label":"Sub-Brand4",
          "value":4
        }
      ],
    }
  },
  computed: {
    ...mapGetters(['wishes','indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        this.$store.dispatch('myWishes').then(res=>{

        });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
        // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);

    },
    deleteWish(id){
       this.$dialog
        .confirm({
          message: "Are you sure to delete from whishlist?",
          confirmButtonText: "confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
           this.$store.dispatch('deleteWish',{id}).then(res=>{
             this.$toast('Delete successfully');
             this.init();
        })
         
        })
        .catch(() => {
          // on cancel
        });
      
    },
      shareShow(item) {
       this.$store
        .dispatch("share", {
          imageUrl: [item.spuMainImage],
          url: "proDetail?spuCode="+item.spuCode, 
          title: item.name,
        })
        .then(res => {});
    },

  getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    }
  },
  
}
</script>

<style lang="scss" scoped>
  @import "../common/css/search.scss";
  ul{
    border-top: 1px solid #f6f6f6;
  }
  .commonPro .downShare{
    bottom: 0;
  }
</style>



